#mainHCE {

   header {
      padding: 0.5rem 0;
      border-bottom: 1px solid $gray-75;
      .headerContainer {
         .headerRow {
            align-items: center;
   
            &__sidebarCollapse {
               margin-right: auto;
               .fa{
                  font-size: 1.5rem;
               }
            }
   
            &__shortcut {
               .headerShortcut{
                  &__item{
                     padding-left: 0;
                  }
               }
            }
   
            &__user {
               .headerUser {
                  align-items: center;
   
                  &__menu {
                     padding-left: 0.5rem;
                     .headerUserMenu{
                        &__toggle{
                           display: flex;
                           align-items: center;
                           font-weight: 600;
                           font-size: 0.9rem;
                           .headerUserMenuToggle{
                              &__avatar {
                                 padding-right: 0;
                                 img {
                                    border-radius: 500PX;
                                    width: 38PX;
                                    height: 38PX;
                                 }
                              }
                              &__nombre {
                                 margin-left: 0.25rem;
                              }
                              &__edad{
                                 margin-left: 0.25rem;
                                 font-style: italic;
                              }
                           }
                           &:after{
                              font-family: "fontawesome";
                              content: "\f078";
                              border: none;
                              font-size: 70%;
                              margin-left: 0.5rem;
                           }
                        }
                        &__dropdown{
                           border: 0;
                           border-radius: $framework-border-radius;
                           padding: 0;
                           margin: 0;
                           box-shadow: $framework-box-shadow;
                           .dropdown-item{
                              padding: 0.5rem 1rem;
                              color: $gray;
                              .fa{
                                 margin-right: 0.5rem;
                              }
                              .edad{
                                 font-style: italic;
                              }
                              &:not(:last-child){
                                 border-bottom: 1px solid $gray-75;
                              }
                              &:hover,
                              &:active,
                              &:focus{
                                 background-color: transparent;
                              }
                              &.title{
                                 pointer-events: none;
                                 color: $color-primary--dark;
                                 font-weight: 600;
                              }
                           }
                           &::before{
                              display: block;
                              position: absolute;
                              content: "";
                              width: 1rem;
                              height: 1rem;
                              background-color: white;
                              top: -3px;
                              left: calc(50% - 0.5rem);
                              transform: rotate(45deg);
                              z-index: -1;
                           }
                        }
                     }
                  }
               }
            }
         }
      }
   }
}