/********* ESTILOS GENRALES **************/
#mainHCEContent {
   .mainHCEContent__sidebar {
      padding-top: 1rem;
      padding-bottom: 1rem;

      >.row {
         &:not(:last-child) {
            margin-bottom: 2rem;
         }
      }

      .mainHCESidebar {
         &__logo {
            .logo-mobile {
               max-width: 250PX;
               margin: auto;
            }
            .btn-close-mobile {
               position: absolute;
               color: rgba(255,255,255,.75);
               top: 0.5rem;
               right: 0.5rem;
               font-size: 1.25rem;
            }
            /*.mainHCESidebarLogo {
               display: flex;

               a {
                  picture {
                     img {
                        max-width: 100%;
                     }
                  }
               }

               &--colapsado {
                  @extend .mainHCESidebarLogo;
                  display: none;
               }
            }*/
         }

         &__list {
            .mainHCESidebarList {
               &__container {
                  list-style: none;
                  margin: 0;
                  padding: 0;
               }

               &__item {
                  &:not(:last-child) {
                     margin-bottom: 1rem;
                  }

                  &:hover,
                  &.active {
                     a {
                        background-color: $color-primary--light;
                     }
                  }

                  a {
                     display: flex;
                     align-items: center;
                     flex-wrap: nowrap;
                     padding: 0.5rem 0.35rem;
                     border-radius: calc(#{$framework-border-radius} / 2);
                     color: white;

                     .fa {
                        font-size: 0.9rem;
                        margin: 0 0.2rem;
                     }

                     p {
                        color: white;
                        margin: 0;
                        width: fit-content;
                     }
                  }
               }

               &__button {
                  margin-top: 2rem;

                  .btn {
                     width: 100%;
                     display: flex;
                     align-items: center;
                     justify-content: space-around;
                  }

                  a {
                     &:last-child {
                        display: none;
                     }
                  }
               }
            }
         }
      }

      &.colapsado {
         .mainHCESidebar {
            &__logo {
               /*.mainHCESidebarLogo {
                  display: none;

                  &--colapsado {
                     display: flex;
                  }
               }*/
            }

            &__list {
               .mainHCESidebarList {
                  &__container {}

                  &__item {
                     /*a {
                        justify-content: center;
                        @include sidebarTransiciones;

                        .fa {
                           font-size: 2rem;
                           @include sidebarTransiciones;
                        }

                        p {
                           display: none;
                        }
                     }*/
                  }

                  /*&__button {
                     a {
                        @include sidebarTransiciones;

                        .fa {
                           font-size: 1.8rem;
                           @include sidebarTransiciones;
                           background-color: transparent;
                           margin: 0 auto;
                           margin-bottom: .2rem;
                        }

                        &:first-child {
                           display: none;
                        }

                        &:last-child {
                           display: block;
                           width: 55px;
                           height: 55px;
                           border-radius: 100%;
                           display: flex;
                           margin: 0 auto;
                        }
                     }
                  }*/
               }
            }
         }
      }
   }
}

/********* ESTILOS XS hasta MD **************/
@include media-breakpoint-down(md) {
   #mainHCE {
      #mainHCEContent {
         .mainHCEContent__sidebar { 
            position: absolute;
            z-index: 2;
            min-height: 100vh;
            overflow-x: hidden;

            .col.mainHCESidebarLogo--colapsado {
               display: none;
            }
            
            .mainHCESidebar__list {
                .mainHCESidebarList__button {
                   margin-top: 0;
                }
               .mainHCESidebarList__container {
                  width: 250px;
                  margin: auto;
               }
            }
         }
      }
   }
}


