@font-face {
   font-family: 'Rubik';
   src: url('./../assets/fonts/Rubik-Regular.ttf') format('ttf');
   font-weight: normal;
   font-style: normal;
   font-display: swap;
}


body {
   font-family: 'Rubik', sans-serif;
   font-weight: 400;
   font-size: $font-size !important;

   p,
   td,
   th,
   label {
      color: $text-color;
   }

   ::-webkit-scrollbar {
      width: 0.5rem;
      height: 0.5rem;
   }

   ::-webkit-scrollbar-track {
      background: rgba(255, 255, 255, 0.5);
   }

   ::-webkit-scrollbar-thumb {
      background: $color-primary;
   }

   ::-webkit-scrollbar-thumb:hover {
      background: $color-primary--dark;
   }
}


#mainHCE {

   h1,
   h2,
   h3,
   h4,
   h5,
   h6,
   strong,
   b {
      color: $color-primary;
   }

   .btn-icon,
   a {
      color: $gray;
      position: relative;
   }

   .btn {
      border-radius: 100PX;
      text-transform: uppercase;

      >.fa {
         background-color: transparent;
      }

      &.btn {

         &-primary,
         &-accent {
            >.fa {
               margin-left: 0.25rem;
               border-radius: 50PX;
               display: initial;
               padding: 5PX 7PX;
               width: 30PX;
               height: 30PX;
               margin-right: -7PX;
               background-color: $color-primary--light;
            }
         }

         &-light {
            color: $text-color;
         }

         &-accent {
            background-color: $color-accent;
            color: white;

            >.fa {
               background-color: $color-accent--light;
               display: flex;
               align-items: center;
               justify-content: center;
            }

            &:hover {
               background-color: $color-accent--dark;
            }
         }
      }

      box-shadow: none !important;
   }

   .tooltip {
      .tooltip-inner {
         background-color: #fff;
         color: $text-color;
         border: 0.0625rem solid $gray-50;
      }

      &.show {
         opacity: 1;
      }

      &.bs-tooltip-top {
         .arrow {
            &::before {
               border-top-color: $gray-100;
            }
         }
      }

      &.bs-tooltip-bottom {
         .arrow {
            &::before {
               border-bottom-color: $gray-100;
            }
         }
      }

      &.bs-tooltip-right {
         .arrow {
            &::before {
               border-right-color: $gray-100;
            }
         }
      }

      &.bs-tooltip-left {
         .arrow {
            &::before {
               border-left-color: $gray-100;
            }
         }
      }
   }

   .contador {
      position: absolute;
      display: block;
      top: 4PX;
      left: 45%;
      background-color: $red;
      line-height: 100%;
      border-radius: 50PX;
      color: white;
      padding: 1PX 4PX;
      font-size: 0.7rem;
   }

   .card {
      border: none;
      border-radius: 0;
      margin-bottom: 2rem;

      &-header {
         padding: 0.25rem 1rem;
         background-color: $color-primary--lightest-x2;
         border-left: $framework-component-border-left;
         border-left-color: $color-primary;
         border-bottom: none;
         border-radius: 0;

         >.row {
            align-items: center;
            min-height: 32PX;
         }

         h3 {
            font-weight: 600;
            font-size: 1rem;
            margin: 0;
         }
      }

      &-body {
         padding: 1.25rem 1rem;
      }

      &-footer {}
   }

   .form-group {
      &.row {
         align-items: center;
         margin: 0;

         label {
            padding-right: 0.25rem;
            margin: 0;
         }

         p {
            padding-left: 0.25rem;
            margin: 0;
            font-weight: 600;
         }
      }

      .form-control {
         padding: 0.25rem;
         height: 32PX;
      }
   }

   .box-secondary {
      margin: 0 0 1rem;
      padding: 1rem 0;
      background-color: $gray-50;
   }

   .componente {
      &MostrarCantidadRegistros {
         align-items: center;
         margin-bottom: 1rem;

         p,
         .form-group {
            margin: 0;
         }

         .control-label-between {
            &:first-child {
               padding-right: 0.5rem;
            }

            +.col {
               padding: 0;

               +.control-label-between {
                  padding-left: 0.5rem;
               }
            }
         }
      }

      &Busqueda {
         .input-group {
            background-color: $gray-50;
            border: $framework-border;
            border-radius: 500PX;

            &-prepend {}

            &-text {
               background: transparent;
               border: none;
               padding: 0.5rem;
               color: $color-primary;
            }

            .form-control {
               background: transparent;
               border: none;
               font-size: 0.9rem;

               &:focus,
               &:active {
                  box-shadow: none;
               }
            }

            ::placeholder {
               font-style: italic;
            }
         }
      }

      &Paginador {
         display: flex;
         justify-content: center;
         color: $text-color;
         font-size: 0.9rem;

         .pagination {
            margin-top: 1.5rem;

            .page-link {
               padding: 0.5rem 0.75rem;
               border-radius: 3px;
               margin: 0rem .25rem;
               background-color: $gray-50;
               border-color: $gray-50;
               transition: .3s;

               &:hover {
                  background-color: $gray-75;
                  border-color: $gray-75;
               }
            }
         }

         .active {

            >.page-link,
            >.page-link:hover {
               background-color: $color-primary;
               border-color: $color-primary;
               color: white;
            }
         }
      }

      &FiltroResultados {
         .filtroResultados {
            &__label {
               p {
                  margin: 0;
                  padding: 3PX 0;
               }
            }

            &__switch {
               padding-left: 0;


               >.badge {
                  font-size: 0.8rem;
                  background: $gray-75;
                  border-radius: 500PX;
                  margin: 0 0.25rem 0.5rem;
                  padding: 0.25rem 0.25rem 0.25rem 0.5rem;

                  a {
                     display: flex;
                     align-items: center;

                     >.badge {
                        &__icon {}

                        &__label {
                           margin: 0;
                           padding: 0 0.5rem 0 0.25rem;
                        }

                        &__status {
                           display: flex;
                           align-items: center;
                           justify-content: center;
                           width: 20PX;
                           height: 20PX;
                           background-color: white;
                           border-radius: 500PX;
                           color: $gray-75;

                           &::before {
                              content: "\f00d";
                           }
                        }
                     }
                  }

                  &.active {
                     background: $color-primary;

                     a {
                        >.badge {
                           &__icon {
                              color: white;
                           }

                           &__label {
                              color: white;
                           }

                           &__status {
                              color: $color-primary;

                              &::before {
                                 content: "\f00c";
                              }
                           }
                        }
                     }
                  }
               }

               >.switch {

                  &__container {
                     display: flex;
                     align-items: center;

                     >.switch {
                        font-size: 0.8rem;
                        background: $gray-75;
                        border-radius: 500PX;
                        width: 45px;
                        padding: 0.25rem;

                        &__label {
                           margin: 0;
                           padding: 3PX 10px;
                        }

                        a {
                           >.switch__status {
                              display: flex;
                              align-items: center;
                              width: 16PX;
                              height: 16PX;
                              background-color: white;
                              border-radius: 500PX;
                              color: $gray-75;
                           }
                        }

                        &.active {
                           background: $color-primary;

                           a {
                              >.switch__status {
                                 margin-left: auto;
                              }
                           }
                        }
                     }
                  }

               }

            }
         }
      }
   }

   .modal {
      .modal-dialog {
         .modal-content {
            .modal {

               &-header,
               &-footer {
                  background-color: $gray-50;
                  padding: 0.5rem 1rem;
                  border: 0;
                  border-radius: 0;
               }

               &-header {
                  align-items: center;

                  >div {
                     position: relative;
                     padding-right: 0.9375rem;
                     padding-left: 0.9375rem;
                  }

                  &__title {
                     flex-basis: 0;
                     flex-grow: 1;
                     max-width: 100%;

                     h5 {
                        font-weight: 600;
                        text-transform: uppercase;
                     }
                  }

                  &__print {}

                  &__close {}
               }

               &-body {
                  max-height: calc(100vw - 630PX);
                  overflow: auto;

                  .report-list {
                     &__title {
                        h6 {
                           padding: 0.5rem 0.75rem;
                           border-bottom: 1px solid $gray-75;
                           border-top: 1px solid $gray-75;
                           color: $text-color;
                           text-transform: uppercase;
                        }
                     }

                     &__description {
                        p {
                           padding: 0.5rem 0.75rem 1rem 0.75rem;
                        }

                        ul {
                           padding: 0.5rem 0.75rem 1rem 1.75rem;
                           color: $text-color;
                        }
                     }

                  }
               }

               &-footer {}
            }
         }

         &.modal-xl {
            max-width: 80vw;
         }
      }
   }

   .cardAccess {
      padding: 0.5rem 1rem;
      margin-bottom: 2rem;
      border-left: $framework-referencias-border-left;

      &__header {
         .cardAccessHeader {
            align-items: center;
            min-height: 48PX;

            &__icon {
               padding-right: 0;

               .fa {
                  font-size: 2rem;
               }
            }

            &__title {
               p {
                  font-weight: 600;
                  margin: 0;
               }
            }

            &__chevron {
               padding-left: 0;

               a {
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  padding: 6PX 7PX 5PX 8PX;
                  max-width: 23PX;
                  background-color: $gray;
                  border-radius: 50PX;

                  .fa {
                     font-size: 0.75rem;
                     color: white;
                  }
               }
            }
         }

         &:not(:only-child) {
            margin-bottom: 0.5rem;
         }
      }

      &__body {
         .cardAccessBody {
            align-items: center;
            min-height: 45PX;

            &__content {
               p {
                  font-size: 0.9rem;
                  margin: 0;
               }
            }
         }
      }

      &--medicamentos {
         @extend .cardAccess;
         background-color: $violet-50;
         color: $violet;
         border-left-color: $violet;

         .cardAccess {
            &__header {
               .cardAccessHeader {
                  &__icon {
                     .fa {
                        color: $violet;
                     }
                  }

                  &__title {
                     p {
                        color: $violet;
                     }
                  }

                  &__chevron {
                     a {
                        background-color: $violet;

                        .fa {
                           color: $violet-50;
                        }
                     }
                  }
               }
            }

            &__body {
               .cardAccessBody {
                  &__content {
                     p {
                        color: $violet;
                     }
                  }
               }
            }
         }
      }

      &--estudios {
         @extend .cardAccess;
         background-color: $turquoise-50;
         color: $turquoise;
         border-left-color: $turquoise;

         .cardAccess {
            &__header {
               .cardAccessHeader {
                  &__icon {
                     .fa {
                        color: $turquoise;
                     }
                  }

                  &__title {
                     p {
                        color: $turquoise;
                     }
                  }

                  &__chevron {
                     a {
                        background-color: $turquoise;

                        .fa {
                           color: $turquoise-50;
                        }
                     }
                  }
               }
            }

            &__body {
               .cardAccessBody {
                  &__content {
                     p {
                        color: $turquoise;
                     }
                  }
               }
            }
         }
      }

      &--profesionales {
         @extend .cardAccess;
         background-color: $teal-50;
         color: $teal;
         border-left-color: $teal;

         .cardAccess {
            &__header {
               .cardAccessHeader {
                  &__icon {
                     .fa {
                        color: $teal;
                     }
                  }

                  &__title {
                     p {
                        color: $teal;
                     }
                  }

                  &__chevron {
                     a {
                        background-color: $teal;

                        .fa {
                           color: $teal-50;
                        }
                     }
                  }
               }
            }

            &__body {
               .cardAccessBody {
                  &__content {
                     p {
                        color: $teal;
                     }
                  }
               }
            }
         }
      }

      &--turnos {
         @extend .cardAccess;
         background-color: $orange-50;
         color: $orange;
         border-left-color: $orange;

         .cardAccess {
            &__header {
               .cardAccessHeader {
                  &__icon {
                     .fa {
                        color: $orange;
                     }
                  }

                  &__title {
                     p {
                        color: $orange;
                     }
                  }

                  &__chevron {
                     a {
                        background-color: $orange;

                        .fa {
                           color: $orange-50;
                        }
                     }
                  }
               }
            }

            &__body {
               .cardAccessBody {
                  &__content {
                     p {
                        color: $orange;
                     }
                  }
               }
            }
         }
      }
   }

   table {
      &.table {

         th,
         td {
            padding: 0.5rem 0.75rem;
            border-top-color: $gray-75;
         }

         thead {
            tr {
               th {
                  font-weight: 600;
                  font-size: 0.8rem;
                  border-bottom: none;

                  &.tableOrderBy {
                     a {
                        .fa {
                           color: $gray-50;
                        }
                     }

                     &--active {
                        @extend .tableOrderBy;

                        a {
                           .fa {
                              color: $color-primary--light;
                           }
                        }
                     }
                  }
               }
            }
         }

         tbody {
            tr {
               &:nth-child(odd) {
                  background-color: $color-primary--lightest-x2;
               }
            }
         }

         &:only-child {
            margin-bottom: 0;
         }

         &.tablaPrescripcionMedicamentos {
            .estadoPrescripcion {
               &--activo {
                  .fa {
                     color: $green;
                  }
               }

               &--inactivo {
                  .fa {
                     color: $red;
                  }
               }

               &--indefinido {
                  .fa {
                     color: $yellow;
                  }
               }

               &--finalizado {
                  .fa {
                     color: $gray-200;
                  }
               }
            }
         }
      }
   }

   #mainHCEContent {
      .mainHCEContent {
         &__sidebar {
            width: 100vw; 
            transform: translate(0, 0);
            @include media-breakpoint-up(lg) {
               transform: translate(0, 0);
            }
            @include media-breakpoint-up(lg) {
               width: $sidebar-width;
            }
            background-color: $color-primary;
            @include sidebarTransiciones;

            &.colapsado {
               width: $sidebar-width--colapsado;
               @include sidebarTransiciones;
               transform: translate(-90PX, 0);
               @include media-breakpoint-up(lg) {
                  transform: translate(0, 0);
               }
            }
         }

         &__body {
            .mainHCEContentBody {

               #contenido {
                  background-color: $gray-50;
                  padding: 1rem;
                  height: calc(100vh - 104PX);
                  overflow-x: hidden;
                  overflow-y: auto;

                  >.container-fluid,
                  >.container {
                     padding: 0;
                  }

                  .contenido {
                     &Title {
                        margin-bottom: 1rem;

                        h1 {
                           margin: 0;
                           font-size: 2rem;
                        }
                     }

                     &Alerts {}

                     &Cards {}

                     &EmptyState {
                        align-items: center;
                        justify-content: center;
                        min-height: calc(50vw - 235PX);
                        margin-bottom: 2rem;

                        &__icon {
                           display: flex;
                           justify-content: center;

                           .fa {
                              font-size: 10rem;
                              color: $color-primary--lightest;
                           }
                        }
                     }
                  }
               }
            }
         }
      }
   }
}

/********* ESTILOS LG hasta XL **************/
@include media-breakpoint-up(lg) {
   #mainHCE #mainHCEContent .mainHCEContent__body .mainHCEContentBody {
      flex-direction: column;

      #contenido {
         padding: 1.5rem 2rem;

         .contenidoTitle {
            h1 {
               font-size: 2.5rem;
            }
         }
      }
   }
}

@media (min-width: 360px) and (max-width: 771px) {
   .btn-imprimir {
      display: none;
   }
   .btn-imprimir-mobile {
      display: block;
   }
   #mainHCE {
      h5 {
         font-size: 15px;
      }
      .form-group.row label {
         padding: 0;
         margin: 0;
      }
      .modal {
         .modal-dialog{
            &.modal-xl {
               max-width: 96vw;
            }
            .modal-content {
               .modal-header {
                  padding: 0.5rem;
                  > div {
                     padding-right: 0;
                     padding-left: 0;
                  }
               }
               .modal-body {
                  max-height: calc(105vw - 20PX);
                  overflow: auto;
                  padding: 0.5rem;
                  .box-secondary {
                     margin: 0 0 1rem;
                     padding: 0.5rem;
                     background-color: #f4f4f6;
                  }
                  .table {
                     td {
                        padding: 0.5rem;
                        border-top-color: #e2e4e8;
                        font-size: 14px;
                     }
                  }
               }
            }
         }
      }
   }
}
@media (min-width: 772px){
   .btn-imprimir {
      display: block;
   }
   .btn-imprimir-mobile {
      display: none;
   }
}