.contenidoAlerts{}

.alertContainer{
   padding-top: 0.5rem;
   padding-bottom: 0.5rem;
   border-left: $framework-component-border-left;
   margin-bottom: 2rem;
   .alertRow{
      align-items: center;
      .alert{
         &__icon{
            padding-right: 0;
         }
         &__message{
            p{
               margin: 0;
            }
            .badge{
               background-color: transparent;
               margin-right: 0.25rem;
               &:not(:nth-child(2)){
                  
               }
            }
         }
         &__close{

         }
      }
   }
   &.resultados{
      .alert {
         &__icon {
            .fa {
               &::before {
                  content: "\F108";
               }
            }
         }
      }
   }
   &.medicamentos {
      .alert {
         &__icon {
            .fa {
               font-family: 'fontInnova' !important;
               vertical-align: 0%;
               &::before {
                  content: "\D008";
               }
            }
         }
      }
   }
   &.internaciones {
      .alert {
         &__icon {
            .fa {
               &::before {
                  content: "\F0F8";
               }
            }
         }
      }
   }
   &--primary {
      @extend .alertContainer;
      color: $color-primary;
      background-color: $color-primary--lightest-x2;
      border-left-color: $color-primary;
      
      .alert {
         &__icon {
            .fa {
               &::before {}
            }
         }
         
         &__message {
            .badge.badge-pill {
               color: white;
               background-color: $color-primary;
            }
            p{
               color: $color-primary;
            }
         }

         &__close {
            a {
               .fa {
                  color: $color-primary;
               }
            }
         }
      }
   }
   &--warning{
      @extend .alertContainer;
      color: $yellow;
      background-color: $yellow-50;
      border-left-color: $yellow;
      .alert{
         &__icon{
            padding-left: 0.25rem;
            .fa{
               font-size: 2rem;
               &::before{
                  content: "\F06A";
               }
            }
         }
         &__message {
            .badge.badge-pill {
               color: white;
               background-color: $yellow;
            }
            p {
               color: $yellow;
            }
         }
         &__close {
            a{
               .fa{
                  color: $yellow;
               }
            }
         }
      }
   }
}